import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOmniScheduledTransfer, IOptionsPsOptionScheduledTransferExposed } from 'src/app/commonBussinessSRC/psComponents/ps-scheduled-transfers-list/ps-scheduled-transfers-list.component.interface';
import { AccountsService } from 'src/app/commonBussinessSRC/psServices/accounts/accounts.service';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { ITransactionResponse } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsDateFormatPipe } from 'src/app/commonSRC/psPipes/ps-date-format/ps-date-format.pipe';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsLabel, IOptionsPsTemplateView } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { OmniBasePage } from '../omni-base/omni-base.page';



@Component({
  selector: 'app-scheduled-transfers-list',
  templateUrl: './scheduled-transfers-list.page.html',
  styleUrls: ['./scheduled-transfers-list.page.scss'],
})
export class ScheduledTransfersListPage extends OmniBasePage implements OnInit {
  requestWasSent = true;
  public showTransaction = true;
  private psDatePipe = new PsDateFormatPipe();
  noTransfersFound = false;
  sheduledTransferListOptions: IOptionsPsOptionScheduledTransferExposed;
  private formGroup = new FormGroup({});

  public templateViewOptions: IOptionsPsTemplateView = {
    group: this.formGroup
  };

  noTransfersOptions: IOptionsPsLabel = {
    labelKey: 'no_transfers_available_key'
  };

  constructor(public logger: LoggerService, private omniPull?: OmniPullService, private accountsService?: AccountsService) {
    super();
  }

  ngOnInit() {
    super.init();
  }
  ionViewWillEnter() {
    super.viewWillEnter();
    this.sheduledTransferListOptions = {
      isEditable: true,
      group: this.formGroup
    };
    this.loadScheduledTransfers();
  }

  //Modified by ghada, AZDB-5993 as discussed with PM to send the right status for each transactions list cash/scheduled/pending(immediate)
  loadScheduledTransfers() {
    let trxStatus = '';
    switch (PsCommonSettings.oper_ID) {
      case CommonBussinessConstant.CASH_TRANSFER_REP_OPER_ID: {
        trxStatus = "P,R,F";
        break;
      }
      case CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID: {
        trxStatus = "A";
        break;
      }
      case CommonBussinessConstant.SCHEDULED_TRANSFERS_OPER_ID: {
        trxStatus = "P,T,A";//jira #7123 - Aline request to include A status in Sch Transfer list as well
        break;
      }
    }
    const generalRequestData: any = {
      commonParametersList: {
        status: trxStatus
        //  fromAmount: "1",
        //  toAmount: "100",
      }
    };
    this.returnScheduledTransfers(generalRequestData);
  }

  private async returnScheduledTransfers(requestData: any) {
    this.requestWasSent = true;
    const result = await this.omniPull.returnTransactionsList(requestData).catch(error => {
      this.logger.error('Error: While fetching scheduled transfers  :', error);
    });

    if (result && result.gridModel.length > 0) {
      this.sheduledTransferListOptions.listOfOptions = this.populateScheduledTransfers(result.gridModel);
    } else {
      this.noTransfersFound = true;
      this.sheduledTransferListOptions.listOfOptions = [];
    }
    this.requestWasSent = false;
  }



  private populateScheduledTransfers(transactionslist: ITransactionResponse[]): IOmniScheduledTransfer[] {
    const transactions: IOmniScheduledTransfer[] = [];
    const psDatePipe = new PsDateFormatPipe();
    for (const iterator of transactionslist) {
      // if (iterator.transferTypeOperId != undefined) {
      const trans: IOmniScheduledTransfer = {
        transferType: (iterator.transferTypeOperId) ? (this.commonProv.getPageByOperId(iterator.transferTypeOperId)?.OPER_NAME ? this.commonProv.translate(this.commonProv.getPageByOperId(iterator.transferTypeOperId)?.OPER_NAME) : iterator.transferType ? this.getTransferTypeInfo(iterator.transferType, 2) : '-----') : '---',
        transactionAmount: iterator.amount,
        transactionAmountNCurrency: this.commonProv.amountFormat(iterator.amount, iterator.currencyDecimalPoints || CommonBussinessConstant.DEFAULT_DECIMAL_POINTS) + ' (' + iterator.currencyBriefNameEnglish + ')',
        transactionStatus: iterator.status,
        statusCode: iterator.status,
        status: iterator.statusDescription,
        statusDescription: iterator.statusDescription,
        fromCurrency: iterator.fromAccountCurrency,
        transactionNumber: iterator.transactionNo,
        fromAccount: iterator.fromAccountAdditionalRef,
        fromAccount_lookupKey: iterator.fromAccountAdditionalRef,
        toAccount: iterator.toAccountNumber ? iterator.toAccountNumber : iterator.toAccountAdditionalRef ? iterator.toAccountAdditionalRef : iterator.toAccountAccGl,
        toAccount_lookupKey: iterator.toAccountNumber ? iterator.toAccountNumber : iterator.toAccountAdditionalRef ? iterator.toAccountAdditionalRef : iterator.toAccountAccGl,
        reference: iterator.soReference || '-----',
        title: PsCommonSettings.oper_ID === CommonBussinessConstant.CASH_TRANSFER_REP_OPER_ID ? iterator.benefName : iterator.fromAccountAdditionalRef,
        subTtile: iterator.amount,
        accGl: iterator.fromAccountAccGl, // for account statement
        currency: iterator.fromAccountCurrency, // for account statement
        workingCif: iterator.fromAccountCif, // for account statement
        serialNo: iterator.fromAccountSerialNo, // for account statement
        formattedToAccount: this.accountsService.returnFormattedAccount({ additionalRef: (iterator.toAccountAdditionalRef || iterator.toAccountNumber), ibanAccNo: iterator.toAccountIbanAccNo }).formattedAccount,
        formattedFromAccount: this.accountsService.returnFormattedAccount({ additionalRef: iterator.fromAccountAdditionalRef, ibanAccNo: iterator.fromAccountIbanAccNo, currency: iterator.fromAccountCurrency }).formattedAccount,
        toBeneficiary: iterator.toAccountAdditionalRef,
        toBeneficiary_lookupKey: iterator.toAccountAdditionalRef,
        toAccountType: this.getTransferAccountType(iterator),
        // toAccountType: iterator.transferType === '1' ? (iterator.toAccountCif === iterator.fromAccountCif) ? 'ownAccountStandingOrder' : 'companyAccountStandingOrder' : iterator.transferType === '2' ? 'bankLocalStandingOrder' : iterator.transferType === '3' ? 'internationalAccountStandingOrder' : iterator.transferType === '4' ? 'companyAccountTransfer' : 'ownAccountStandingOrder',
        // toAccountType: iterator.transferType == '2' ? ConstantCommon.INTERNAL_TRANSACTION_TYPE : OWN_ACCOUNT_TRANSFER_TYPE == '1' ? (iterator.fromAccountCif == iterator.toAccountCif ? ConstantCommon.OWN_TRANSACTION_TYPE : ConstantCommon.LOCAL_TRANSACTION_TYPE) : iterator.transferType == '3' ? ConstantCommon.INTERNATIONAL_TRANSACTION_TYPE : ConstantCommon.OWN_TRANSACTION_TYPE,
        scheduler: PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID||PsCommonSettings.oper_ID === CommonBussinessConstant.CASH_TRANSFER_REP_OPER_ID ? false : true, // related to next step in payment
        // scheduler: true, //related to next step in payment
        // operId: iterator.transferTypeOperId ? parseInt(iterator.transferTypeOperId) : iterator.transferType ? this.getTransferTypeInfo(iterator.transferType, 1) : '', // By Mohanad AZDB-6915 - 26012025
        operName: (iterator.transferTypeOperId) ? this.commonProv.getPageByOperId(iterator.transferTypeOperId)?.OPER_NAME ? this.commonProv.getPageByOperId(iterator.transferTypeOperId)?.OPER_NAME : '' : '', //added by ghada, AZDB-5974 for opening each transfer screen with it's related oper name
        transferTypeOperId: iterator.transferTypeOperId ? parseInt(iterator.transferTypeOperId) : iterator.transferType ? this.getTransferTypeInfo(iterator.transferType, 1) : '',
        date: iterator.valueDateStr,   // AZDB-6090
        bankCifShortNameEng: iterator.bankCifShortNameEng || '--------',
        transactionDate: iterator.transactionDateStr,  // AZDB-6090
        branch: iterator.fromAccountBranch,
        benefName: iterator.benefName,
        idTypeNumber: iterator.idTypeNumber, //iterator.idType + '/' + removed upon a request from Lina Matta since both values are available under "idNumber"
        purpose: iterator.purpose,
        idNumber: iterator.idNumber,
        idType: iterator.idType,
        idExpiryDate: iterator.idExpiryDate,
        idIssueDate: iterator.idIssueDate,
        idCountry: iterator.idCountry,
        benefPhone: iterator.benefPhone,
        purposeTxtDesc: iterator.purpose,
        toBranch: iterator.toAccountBranch,
        transactionBranch: iterator.transactionBranch ////Jira #5525 On Behalf Of Rakesh, Include transactionBranch in the list
      };
      if (iterator.transferType == CommonBussinessConstant.OWN_ACCOUNT_TRANSFER_TYPE || iterator.transferType == CommonBussinessConstant.INTERNAL_TRANSFER_TYPE) {
        delete trans.bankCifShortNameEng;
      }
      transactions.push(trans);
    }
    if (PsCommonSettings.custMode) {
      const scheduledTransactions = transactions.filter(trans => (trans.transferTypeOperId == CommonBussinessConstant.OWN_ACCOUNT_STANDING_ORDER_OPER_ID || trans.transferTypeOperId == CommonBussinessConstant.INTERNAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID || trans.transferTypeOperId == CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID || trans.transferTypeOperId == CommonBussinessConstant.EXTERNAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID));
      const immedTransactions = transactions.filter(trans => (trans.transferTypeOperId == CommonBussinessConstant.OWN_ACCOUNT_TRANSFER || trans.transferTypeOperId == CommonBussinessConstant.INTERNAL_BENEFICIARY_TRANSFER || trans.transferTypeOperId == CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER || trans.transferTypeOperId == CommonBussinessConstant.INTERNATIONAL_BENEFICIARY_TRANSFER));
      return PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID ? immedTransactions : scheduledTransactions;
    }
    return transactions;
  }



  getTransferTypeInfo(transferType, type) {
    let transferTypeOperId;
    if (transferType === CommonBussinessConstant.LOCAL_TRANSFER_TYPE) {
      transferTypeOperId = PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID ? CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER : CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID;
    } else if (transferType === CommonBussinessConstant.INTERNATIONAL_TRANSFER_TYPE) {
      transferTypeOperId = PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID ? CommonBussinessConstant.INTERNATIONAL_BENEFICIARY_TRANSFER : CommonBussinessConstant.EXTERNAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID;
    } else if (transferType === CommonBussinessConstant.OWN_ACCOUNT_TRANSFER_TYPE) {
      transferTypeOperId = PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID ? CommonBussinessConstant.OWN_ACCOUNT_TRANSFER : CommonBussinessConstant.OWN_ACCOUNT_STANDING_ORDER_OPER_ID;
    } else if (transferType === CommonBussinessConstant.INTERNAL_TRANSFER_TYPE) {
      transferTypeOperId = PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID ? CommonBussinessConstant.INTERNAL_BENEFICIARY_TRANSFER : CommonBussinessConstant.INTERNAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID;
    }

    if (type === 1) {
      return transferTypeOperId;
    } else {
      return this.commonProv.getPageByOperId(transferTypeOperId)?.OPER_NAME ? this.commonProv.translate(this.commonProv.getPageByOperId(transferTypeOperId)?.OPER_NAME) : '';
    }
  }

  getTransferAccountType(iterator) {
    let toAccountType = '';
    if (iterator.transferTypeOperId) {
      if ((iterator.transferTypeOperId == CommonBussinessConstant.OWN_ACCOUNT_TRANSFER || iterator.transferTypeOperId == CommonBussinessConstant.OWN_ACCOUNT_STANDING_ORDER_OPER_ID) && iterator.toAccountCif == iterator.fromAccountCif) {
        toAccountType = iterator.transferTypeOperId == CommonBussinessConstant.OWN_ACCOUNT_TRANSFER ? 'ownAccountTransfer' : 'ownAccountStandingOrder';
      } else if (iterator.transferTypeOperId == CommonBussinessConstant.INTERNAL_BENEFICIARY_TRANSFER || iterator.transferTypeOperId == CommonBussinessConstant.INTERNAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID) {
        toAccountType = iterator.transferTypeOperId == CommonBussinessConstant.INTERNAL_BENEFICIARY_TRANSFER ? 'companyAccountTransfer' : 'companyAccountStandingOrder'
      } else if (iterator.transferTypeOperId == CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER || iterator.transferTypeOperId == CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID) {
        toAccountType = iterator.transferTypeOperId == CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER ? 'bankLocalTransfer' : 'bankLocalStandingOrder'
      } else if (iterator.transferTypeOperId == CommonBussinessConstant.INTERNATIONAL_BENEFICIARY_TRANSFER || iterator.transferTypeOperId == CommonBussinessConstant.EXTERNAL_BENEFICIARY_TRANSFER_STANDING_ORDER_OPER_ID) {
        toAccountType = iterator.transferTypeOperId == CommonBussinessConstant.INTERNATIONAL_BENEFICIARY_TRANSFER ? 'internationalAccountTransfer' : 'internationalAccountStandingOrder'
      } else {
        toAccountType = iterator.transferTypeOperId == CommonBussinessConstant.CASH_BENEFICIARY_OPER_ID ? 'cashTransfer' : '';
      }
    }
    return toAccountType;
  }
}